@import url('https://fonts.googleapis.com/css?family=Titillium+Web');

body {
    font-family: 'Titillium Web', sans-serif, 14px;
}

body h1,h4 {
    text-align: center
}
.slider-box {
    box-sizing: border-box;
    background-color: #05ffb0;
    border: 2px solid black;
    width: 50%; 
    margin-left: 300px;
    margin-right: 10px;
    margin-bottom: 10px;
    
}

.slider-box:nth-child(2){
    text-align: center;
}

.slider-item {
    cursor: pointer;
    display: inline-block;
    width: 60px;
}

.slider-item-2 {
    text-align: center;
    cursor: pointer;
    display: inline-block;
    width: 250px;
    margin-bottom: 2px;
}

.view-box {
    cursor: pointer;
    text-align: center;
    width:250px;
    height: 50px;
    box-sizing: border-box;
    border: 2px solid black;
    background-color: rgba(73, 200, 225, 0.598);
    margin-left: 380px;
}


.calendar {
    float: left;
    width: 75%;
    height: 620px;
    box-sizing: border-box;
    background-color: rgba(11, 0, 12, 0.45);
    border: 2px solid black;
    overflow: auto;

}

.right-box {
    float: right;
    width: 23%;
    height: 200px;
    box-sizing: border-box;
    background-color: rgba(176, 77, 181, 0.71);
    border: 2px solid black;
    margin-bottom: 10px;
    margin-right: 15px;   
}


#add-button {
    width: 100px;
    height: 30px;
    box-sizing: border-box;
    background-color: rgb(0, 0, 0); 
    color: white;
    margin-left: 100px;
}

.cell-box {
    cursor: pointer;
    text-align: center;
    box-sizing: border-box;
    height: 80px;
    background-color: rgb(189, 101, 184);
    border: 1px solid black;
    width: 100px;
}

tr th {
    text-align: center;
}

.star{
    cursor: pointer;
    font-size: 20px;
    margin-left: 400px;
    box-sizing: border-box;
}


